.about__title, .shows__title, .contact__title, .podcastVoices__title, .podcastMain__title, .classesMain__toDownLink, .classesMain__title {
  font-family: Roboto;
  font-weight: bold;
  font-size: 4.5rem;
  color: #ffffff;
}

.alert__title, .whyListItem__itemTitle, .contactCard__title, .showsBody__title, .modal__legend {
  font-family: Roboto;
  font-weight: bold;
  font-size: 2.5rem;
  color: #ffffff;
}

.contactCard__info, .contactCard__subtitle, .contactForm__input, .contactForm__label, .modal__input, .modal__label, .message, .error__paragraph {
  font-family: Roboto;
  font-weight: normal;
  font-size: 1.25rem;
  color: #ffffff;
}

.alert__staff, .alert__mainText, .whyListItem__itemBody, .contactForm__privatePolicyLabel, .contactForm__privatePolicyInput, .showCard__place, .modal__privatePolicyLabel, .modal__privatePolicyInput, .about__text, .shows__subtitle, .podcastMain__mainText, .classesMain__mainText {
  font-family: Roboto;
  font-weight: normal;
  font-size: 1rem;
  color: #ffffff;
}

.contactForm__button, .monthButton, .showsCarouselContainer__reserveButton, .modal__submitButton {
  font-family: Roboto;
  font-weight: bold;
  font-size: 1.2rem;
  color: #ffffff;
}

.error__title, .why__title, .why__mark, .home__floatTitleText, .home__title {
  font-family: Poppins;
  font-weight: bold;
  font-size: 7rem;
  color: #ffffff;
}

.home__nextShowText {
  font-family: Poppins;
  font-weight: normal;
  font-size: 1.5rem;
  color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: content-box;
  cursor: none;
}

body {
  background: #202020;
  color: #ffffff;
  overflow-x: hidden;
}

.App {
  overflow-x: hidden;
}

section {
  /* width: 65vmax; */
  /* max-width: 90%; */
  margin-top: calc(4.5vh + 2.2rem);
  height: calc(95.5vh - 2.2rem);
}

a:link {
  color: #05c4d3;
  text-decoration: underline;
}
a:visited {
  color: #05c4d3;
  text-decoration: none;
}

ul {
  list-style: none;
}

button {
  border: solid 1px #05c4d3;
  cursor: pointer;
  line-height: 2.5em;
  background: transparent;
  text-transform: uppercase;
}
button:hover {
  background: #05c4d3;
}

