.contact {
  position: fixed;
  top: 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  gap: 6vh;
  min-height: calc(95.5vh - 2.2rem - 4rem);
  padding: 0;
}
.contact__title {
  text-transform: uppercase;
  color: #05c4d3;
}
.contact__body {
  display: grid;
  min-height: 55vh;
  grid-template-columns: repeat(12, 1fr);
  gap: 1.7%;
}
.contact__loading {
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 0;
  max-width: 100vw;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 8;
  transition: all 0.5s;
  backdrop-filter: blur(2px);
}

@media (max-width: 850px) {
  .contact {
    padding: 2rem 0;
  }
}
@media (max-width: 160vh) {
  .contact {
    gap: 5vh;
  }
  .contact__body {
    gap: 5vh;
  }
}
@media (max-width: 65vh) {
  .contact {
    padding: 2rem 8vw;
    gap: 4vh;
    min-height: calc(95.5vh - 2.2rem);
    height: unset;
  }
  .contact__body {
    display: flex;
    flex-flow: column nowrap;
    gap: 4vh;
  }
}
@media (max-width: 500px) {
  .contact {
    max-height: unset;
    height: unset;
    position: static;
    top: unset;
    gap: 4vh;
    padding: 1rem 10vw 3rem;
    box-sizing: border-box;
  }
  .contact__title {
    font-size: 4rem;
  }
  .contact__body {
    gap: 4vh;
  }
}
.home {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  gap: 3vh;
}
.home__heading {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 1.7%;
}
.home__title {
  grid-column: 1/10;
  white-space: nowrap;
}
.home__titleSmile {
  color: #05c4d3;
}
.home__nextShowInfo {
  grid-column: 10/13;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  gap: 1rem;
}
.home__nextShowText {
  white-space: nowrap;
  text-transform: uppercase;
}
.home__nextShowText--pink {
  color: #d4009f;
}
.home__floatTitle {
  position: relative;
  height: 7rem;
  left: -17.5vmax;
  white-space: nowrap;
}
.home__floatTitleText {
  display: flex;
  position: absolute;
  text-transform: uppercase;
  color: transparent;
  line-height: 1em;
  text-align: center;
  font-size: 8rem;
}
.home__floatTitleText--left {
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #d4009f;
  animation: translateToLeft 10s linear infinite;
}
.home__floatTitleText--right {
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #05c4d3;
  animation: translateToRight 15s linear infinite;
}
.home__floatTitleSpan {
  display: inline-block;
  line-height: 1em;
}
.home__floatTitleSpan--left {
  width: 95.5rem;
}
.home__floatTitleSpan--right {
  width: 182.5rem;
}

@media (max-width: 700px) {
  .home__heading {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    gap: 1rem;
    align-items: center;
  }
}
@media (min-width: 700px) and (max-width: 900px) {
  .home__title {
    font-size: 6rem;
    grid-column: 1/9;
  }
  .home__nextShowInfo {
    grid-column: 9/13;
  }
  .home__nextShowText {
    font-size: 1.4rem;
    gap: 0.8rem;
  }
}
@media (max-width: 700px) {
  .home__title {
    font-size: 5.5rem;
  }
}
@keyframes translateToLeft {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-95.5rem);
  }
}
@keyframes translateToRight {
  0% {
    transform: translateX(-182.5rem);
  }
  100% {
    transform: translateX(0);
  }
}
