.why {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: 2.15%;
  height: calc(95.5vh - 2.2rem - 4rem);
  padding: 2rem 0;
  align-content: center;
  z-index: 1;
}
.why__questionMarks {
  position: absolute;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  overflow: hidden;
  z-index: 0;
}
.why__mark {
  position: absolute;
  font-size: 60rem;
  text-transform: uppercase;
  color: transparent;
  -webkit-text-stroke-width: 1px;
  opacity: 0.6;
  line-height: 0.5em;
  user-select: none;
  z-index: 0;
}
.why__mark--open {
  -webkit-text-stroke-color: #05c4d3;
  top: 0;
  left: -5vmax;
}
.why__mark--close {
  -webkit-text-stroke-color: #d4009f;
  bottom: 0;
  right: -5vmax;
}
.why__titleContainer {
  position: relative;
  grid-column: 1/6;
  display: flex;
  align-items: flex-end;
  z-index: 2;
}
.why__title {
  font-weight: bold;
  letter-spacing: 0.01rem;
  line-height: 1.3em;
  font-size: 6.5rem;
  text-transform: uppercase;
}
.why__listContainer {
  height: 80vh;
  grid-column: 6/13;
}
.why__list {
  display: grid;
  grid-template-rows: auto;
  gap: 5vh;
  justify-content: center;
  align-content: space-evenly;
  height: 100%;
}

@media (max-width: 500px) {
  .why__mark {
    font-size: 45rem;
