.arrow {
  position: absolute;
  top: 4vmin;
  height: 4vmin;
  cursor: pointer;
  z-index: 2;
}
.arrow--left {
  left: 0;
}
.arrow--right {
  right: 0;
}

@media (max-width: 500px) {
  .arrow {
    top: 6vmin;
    height: 6vmin;
  }
}
.cursor {
  position: fixed;
  top: 0;
  left: 0;
  width: 5rem;
  height: 5rem;
  pointer-events: none;
  z-index: 9;
}
.cursor__outerCircle {
  position: absolute;
  border-radius: 50%;
  height: 1rem;
  width: 1rem;
  border: 2px solid #05c4d3;
  background: transparent;
  transform: translate(-50%, -50%);
  z-index: 9;
  pointer-events: none;
}
.cursor__innerCircle {
  position: absolute;
  border-radius: 50%;
  height: 0.3rem;
  width: 0.3rem;
  background: #05c4d3;
  transform: translate(-50%, -50%);
  z-index: 9;
  pointer-events: none;
  transition-duration: 0.2s;
}

@media (pointer: coarse) {
  .cursor {
    display: none !important;
  }
  .cursor__outerCircle {
    display: none !important;
  }
  .cursor__innerCircle {
    display: none !important;
  }
}
.classPhoto {
