.cursor
    position: fixed
    top: 0
    left: 0
    width: 5rem
    height: 5rem
    pointer-events: none
    z-index: 9
    &__outerCircle
        position: absolute
        border-radius: 50%
        height: 1rem
        width: 1rem
        border: 2px solid $lightBlue
        background: transparent
        transform: translate(-50%,-50%)
        z-index: 9
        pointer-events: none

    &__innerCircle
        position: absolute
        border-radius: 50%
        height: .3rem
        width: .3rem
        background: $lightBlue
        transform: translate(-50%,-50%)
        z-index: 9
        pointer-events: none
        transition-duration: 0.2s

@media (pointer: coarse)
    .cursor
        display: none !important
        &__outerCircle
            display: none !important

        &__innerCircle
            display: none !important
