.classesPhotos
    display: flex
    height: calc(100vh - 4.5vh - 2.2rem - 4rem)
    padding: 2rem 0
    justify-content: center
    align-items: center
    z-index: 1
    &__photosBody
        display: flex
        flex-flow: row wrap
        width: 100%
        justify-content: center
        align-items: center

    &__photoContainer
        max-width: 45%
        object-fit: contain

@media  (max-width: 100vh)
    .classesPhotos
        &__photosBody
            flex-flow: column nowrap

        &__photoContainer
            max-width: 70%
